/* harmony default export */ var mine = ({
  data: function data() {
    return {
      questionnaires: [],
      typecontent: '3',
      sortcontent: 'tonew',
      types: [{ text: '全部类型', value: '3' }, { text: '未发布', value: '0' }, { text: '收集中', value: '1' }, { text: '已暂停', value: '2' }],
      sorts: [{ text: '从新到旧', value: 'toold' }, { text: '从旧到新', value: 'tonew' }]
    };
  },
  beforeRouteEnter: function beforeRouteEnter(to, from, next) {
    next(function (vm) {
      vm.$http.get('loginStatus').then(function (response) {
        if (response.body.status === 0) window.location.href = "http://survey.twtstudio.com/api/login";else {
          vm.$http.get('minequestion/mine').then(function (response) {
            console.log(response.body);
            // console.log('hhh')
            vm.questionnaires = response.body.questionnaire;
            var arr = vm.questionnaires;
            for (var i = 0; i < arr.length; i++) {
              arr[i].sortnumber = Date.parse(arr[i].created_at) / 1000;
              // console.log(arr[i].sortnumber)
              var currentTime = Date.parse(new Date());
              var dateTime = arr[i].created_at;
              var dday = Date.parse(new Date(dateTime));
              var day = Math.abs(parseInt((dday - currentTime) / 1000 / 3600 / 24));
              var hour = Math.abs(parseInt((dday - currentTime) / 1000 / 3600));
              var minutes = Math.abs(parseInt((dday - currentTime) / 1000 / 60));
              var seconds = Math.abs(parseInt((dday - currentTime) / 1000));
              if (day >= 2) {
                arr[i].created_at = parseInt(day) + '天前';
              } else if (day > 0 && day < 2) {
                arr[i].created_at = '昨天';
              } else if (hour > 0 && hour < 24) {
                arr[i].created_at = parseInt(hour) + '小时前';
              } else if (minutes > 0 && minutes < 60) {
                arr[i].created_at = parseInt(minutes) + '分钟前';
              } else if (seconds > 0 && seconds < 60) {
                arr[i].created_at = parseInt(seconds) + '秒前';
              }
            }
            vm.questionnaires.sort(function (a, b) {
              return a.sortnumber - b.sortnumber;
            });
          });
        }
      });
    });
  },

  methods: {
    edit: function edit(index, id) {
      if (this.questionnaires[index].status != 0) {
        this.$Notice.warning({
          title: '该问卷不能编辑',
          desc: '文件已经成功发布了，不能再编辑了哦',
          top: 200
        });
      } else {
        this.$store.commit('saveid', id);
        this.$router.push({ path: 'edit/' + id });
      }
    },
    set: function set(id) {
      this.$router.push({ path: '/chart/set/' + id });
      this.$store.commit('saveid', id);
    },
    record: function record(id) {
      this.$router.push({ path: '/chart/record/' + id });
      this.$store.commit('saveid', id);
    },
    summary: function summary(id) {
      this.$router.push({ path: '/chart/summary/' + id });
      this.$store.commit('saveid', id);
    },
    answer: function answer(id) {
      this.$router.push({ path: '/answer/' + id });
      this.$store.commit('saveid', id);
    },
    sortby1: function sortby1(a, b) {
      // console.log(b.sortnumber - a.sortnumber)
      return b.sortnumber - a.sortnumber;
    },
    sortby2: function sortby2(a, b) {
      // console.log(a.sortnumber - b.sortnumber)
      return a.sortnumber - b.sortnumber;
    },
    changesort: function changesort() {
      // var data = this.questionnaires
      // console.log(data)
      if (this.sortcontent == 'toold') {
        this.questionnaires.sort(this.sortby1);
      }
      if (this.sortcontent == 'tonew') {
        this.questionnaires.sort(this.sortby2);
      }
      // console.log(data)
      // this.questionnaires = data
    }
  },
  create: function create() {
    // this.changesort()
  }
});
