v<template>
  <div class="mine">
  <div class="mineheader">
    <div class="minenav">
      <div class="navleft">
        <div class="cimgwrapper">
          <img src="./filter.png">
        </div>
        <div class="selectwrapper">
          <select class="selectclass" v-model="typecontent">
            <option v-for="type in types" :value="type.value">{{ type.text }}</option>
          </select>
        </div>
        <div class="selectwrapper">
          <select class="selectclass" style="margin-left: 15px;" v-model="sortcontent" @change="changesort">
            <option v-for="sort in sorts" :value="sort.value">{{ sort.text }}</option>
          </select>
        </div>
        <div>
        </div>
      </div>
      <!-- <div class="navright">
        <div class="inputwrapper" style="width: 200px">
          <input type="text">
        </div>
        <div class="cimgwrapper">
          <img src="./search.png">
        </div>
        <div>
        </div>
      </div> -->
    </div>
  </div>

  <div class="content">
    <div class="contentwrapper">
      
      <div v-for="(questionnaire,index) in questionnaires" class="cardwrapper" v-if="typecontent == questionnaire.status || typecontent == 3">
        <div class="card">
          <!-- {{questionnaires[index].status}} -->
          <div class="cardinner">
            <div class="cardtitle">{{questionnaire.name}}</div>
            <div class="cardtime">{{questionnaire.created_at}}</div>
            <div v-if="questionnaire.status === 0" class="cardstate1">未发布</div>
            <div v-if="questionnaire.status === 1" class="cardstate1 ing">收集中</div>
            <div v-if="questionnaire.status === 2" class="cardstate1 stop">暂停收集</div>
            <div class="cardstate2">已回收问卷</div>
            <div class="cardstate3"><span>{{questionnaire.recovery}}</span>份</div>
          </div>
          <div class="cardhidden">
            <div class="cardimg">
            <!-- <router-link :to="{path: '/create'}"> -->
              <img @click="edit(index,questionnaire.qnid)" src="./edit.png">
          <!--   </router-link> -->
            </div>
            <div class="cardimg">
              <img  @click="set(questionnaire.qnid)" src="./set.png">
            </div>
            <div class="cardimg">
          <!--   <router-link :to="{path: '/chart/record'}"> -->
              <img @click="record(questionnaire.qnid)" src="./chart.png">
            <!-- </router-link> -->
            </div>
            <div class="cardimg">
            <!-- <router-link :to="{path: '/chart/summary'}"> -->
              <div @click="summary(questionnaire.qnid)" class="moreoperation">...</div>
           <!--  </router-link> -->
            </div>
            <div class="cardimg answer">
              <img @click="answer(questionnaire.qnid)" src="./answer1.png">
            </div>
          </div>
        </div>
      </div>
      <router-link :to="{path: '/create'}">
        <div class="cardwrapper">
          <div class="card addcard">
            <img class="grey" src="./add3.png">
            <img class="blue" src="./add2.png">
          </div>
        </div>  
      </router-link>
    </div>
  </div>
  </div>
</template>

<script type="text/ecmascript-6">
export default {
  data () {
    return {
      questionnaires: [],
      typecontent: '3',
      sortcontent: 'tonew',
      types: [
        {text: '全部类型', value: '3'},
        {text: '未发布', value: '0'},
        {text: '收集中', value: '1'},
        {text: '已暂停', value: '2'}
      ],
      sorts: [
        {text: '从新到旧', value: 'toold'},
        {text: '从旧到新', value: 'tonew'}
      ]
    }
  },
  beforeRouteEnter (to, from, next) {
    next(vm => {
      vm.$http.get('loginStatus').then(response => {
        if (response.body.status === 0) window.location.href="http://survey.twtstudio.com/api/login"
        else {
      vm.$http.get('minequestion/mine').then((response) => {
        console.log(response.body)
        // console.log('hhh')
        vm.questionnaires = response.body.questionnaire
        var arr = vm.questionnaires
        for (var i = 0; i < arr.length; i++) {
          arr[i].sortnumber = Date.parse(arr[i].created_at) / 1000
          // console.log(arr[i].sortnumber)
          var currentTime = Date.parse(new Date())
          var dateTime = arr[i].created_at
          var dday = Date.parse(new Date(dateTime))
          var day = Math.abs(parseInt((dday - currentTime) / 1000 / 3600 / 24))
          var hour = Math.abs(parseInt((dday - currentTime) / 1000 / 3600))
          var minutes = Math.abs(parseInt((dday - currentTime) / 1000 / 60))
          var seconds = Math.abs(parseInt((dday - currentTime) / 1000))
          if (day >= 2) {
            arr[i].created_at = parseInt(day) + '天前'
          } else if (day > 0 && day < 2) {
            arr[i].created_at = '昨天'
          } else if (hour > 0 && hour < 24) {
            arr[i].created_at = parseInt(hour) + '小时前'
          } else if (minutes > 0 && minutes < 60) {
            arr[i].created_at = parseInt(minutes) + '分钟前'
          } else if (seconds > 0 && seconds < 60) {
            arr[i].created_at = parseInt(seconds) + '秒前'
          }
        }
        vm.questionnaires.sort((a, b) => {
          return a.sortnumber - b.sortnumber
        })
      })
        }
      })
    })
  },
  methods: {
    edit (index, id) {
      if (this.questionnaires[index].status != 0) {
        this.$Notice.warning({
          title: '该问卷不能编辑',
          desc: '文件已经成功发布了，不能再编辑了哦',
          top: 200
        })
      }
      else {
        this.$store.commit('saveid', id)
        this.$router.push({path: 'edit/'+id })        
      }
    },
    set (id) {
      this.$router.push({path: '/chart/set/'+id })
      this.$store.commit('saveid', id)
    },
    record (id) {
      this.$router.push({path: '/chart/record/'+id })
      this.$store.commit('saveid', id)
    },
    summary (id) {
      this.$router.push({path: '/chart/summary/'+id })
      this.$store.commit('saveid', id)
    },
    answer (id) {
      this.$router.push({path: '/answer/'+id })
      this.$store.commit('saveid', id)
    },
    sortby1 (a, b) {
      // console.log(b.sortnumber - a.sortnumber)
      return b.sortnumber - a.sortnumber
    },
    sortby2 (a, b) {
      // console.log(a.sortnumber - b.sortnumber)
      return a.sortnumber - b.sortnumber
    },
    changesort () {
      // var data = this.questionnaires
      // console.log(data)
      if (this.sortcontent == 'toold') {
        this.questionnaires.sort(this.sortby1)
      }
      if (this.sortcontent == 'tonew') {
        this.questionnaires.sort(this.sortby2)
      }
      // console.log(data)
      // this.questionnaires = data
    }
  },
  create () {
    // this.changesort()
  }
}
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
.mine
  height 100%
  overflow hidden
.mineheader
  height 70px
  position fixed
  left 0px
  right 0px
  top 60px
.minenav
  max-width 1095px
  height 70px
  margin 0 auto
  line-height 70px
.navright div
  display inline-block
  height 70px
  width 130px
  float right
  vertical-align middle
.cimgwrapper
  width 80px !important
.navleft div
  display inline-block
  height 70px
  width 130px
  float left
  vertical-align middle
.cimgwrapper span
  vertical-align middle
  color #505050
  margin-left 17px
.cimgwrapper img
  width 30px
  vertical-align middle
  margin-left 30px
  cursor pointer
.navleft .selectclass
  display inline-block
  height 30px
  margin auto 0
  width 120px
  border-radius 3px
  color #707070
.selectclass:focus
  outline none
.navright img
  width 25px
.navright input
  width 100%
  height 25px
  border 1px solid #dadada
  color #707070
  padding 2px
  border-radius 2px
.inputwrapper input:focus
  outline none
.content
  height calc(100% - 130px)
  background-color rgba(233,234,238,0.52)
  position relative
  top 130px
  overflow-y auto
.contentwrapper
  max-width 1150px
  height 100%
  margin 0 auto
  padding-top 35px
  // padding-bottom 1500px
.cardwrapper
  width 20%
  min-width 180
  height 280px
  box-sizing border-box
  float left
  padding 4px 20px 20px 20px
@media screen and (max-width:1000px){
 .cardwrapper{
   width 25%
  }
}
@media screen and (max-width:800px){
 .cardwrapper{
   width 33.3%
  }
}
@media screen and (max-width:600px){
 .cardwrapper{
   width 50%
  }
}
.card
  width 100%
  height 100%
  background-color white
  border:1px solid #dddee1;
  position relative
.card:hover
  box-shadow 0 1px 6px rgba(0,0,0,.2)
  border-color #eee
.cardinner
  width 80%
  height 100%
  margin auto
  position relative
  padding-top 30px
  box-sizing border-box
.cardtitle
  font-size 14px
.cardtime
  font-size 10px
  color #707070
  margin-top 20px
.cardstate1
  position absolute
  bottom 95px
  font-size 11px
  color #818a91
.ing
  color #5cb85c
.stop
  color #d9534f
.cardstate2
  position absolute
  bottom 70px
  font-size 11px
.cardstate3
  position absolute
  bottom 55px
  font-size 11px
.cardstate3 span
  color rgba(0,161,233,0.79)
.cardhidden
  position absolute
  width 100%
  height 92px
  background-color #e8e8e8
  border-top 1px solid #dddee1
  z-index 100
  bottom -1px
  opacity 0
  transition all 0.4s
.cardhidden .cardimg
  width 33.3%
  height 50%
  text-align center
  vertical-align middle
  box-sizing border-box
  float left
  padding-top 15px
.cardimg img
  width 25px
  cursor pointer
.moreoperation
  font-size 30px
  color #707070
  position relative
  bottom 22px
  cursor pointer
.card:hover .cardhidden
  opacity 1
.addcard:hover
  border-color #34c0ff
  transition all 0.5s
.addcard img
  width 45px
  display inline-block
.addcard
  text-align center
  vertical-align middle
  line-height 285px
.grey
  display inline-block
.blue
  display none !important
.addcard:hover .grey
  display none !important
.addcard:hover .blue
  display inline-block !important
.center-right
  float right
.answer img
  width 30px
  position relative
  bottom 4px
</style>



